(function() {
    // 'use strict';

    angular
        .module('phoenix')
        .controller('webideCtrl', webideCtrl)
        .filter('trustWebideUrl', ['$sce', function($sce) {
            return function(val) {
                val = val + "/"
                return $sce.trustAsResourceUrl(val);
            };
        }]);

    webideCtrl.$inject = ['$scope', '$timeout', 'usSpinnerService', '$uibModal', '$state', 'stuCourseSrv', '$stateParams', '$window', 'webIdeUrl'];

    function webideCtrl($scope, $timeout, usSpinnerService, $uibModal, $state, stuCourseSrv, $stateParams, $window, webIdeUrl) {

        $scope.webideBaseUrl = webIdeUrl + "/ws/";

        $scope.webideServiceUrl = null;
        $scope.webideServiceId = null;
        $scope.webideServiceName = null;

        $scope.display_right_bar = true;
        $scope.md_full_screen = false;

        $scope.loaded = false;

        $scope.toggle_right_bar = function () {
            $scope.display_right_bar = !$scope.display_right_bar;
        }

        $scope.full_md_screen = function () {
            $scope.md_full_screen = !$scope.md_full_screen;

            if ($scope.md_full_screen) {
                angular.element('#resizeDiv').hide();
                angular.element('#leftNav').hide();
                angular.element('#design').width("100%");
            } else {
                angular.element('#resizeDiv').show();
                angular.element('#leftNav').show();
                angular.element('#design').width("66.5%");
                angular.element('#leftNav').width("33%");
            }
        }

        var initRequest = function () {
            // Homework
            if ($stateParams.type === '0') {
                var param = {
                    "homeworkId": $stateParams.homeworkId,
                    "studentId": $stateParams.studentId
                }

                stuCourseSrv.createHwCloudware().save(param).$promise.then(function(response) {
                    if (response.errorCode == 0) {
                        $scope.loaded = true;
                        $scope.webideServiceUrl = response.data.webSocket;
                        $scope.webideServiceId = response.data.serviceId;
                        $scope.webideServiceName = response.data.serviceName;
                        var webIdeUrl = $scope.webideBaseUrl + $stateParams.studentId+ '_' + $stateParams.homeworkId + "/?wsUrl=" +  $scope.webideServiceUrl
                        console.log('webIdeUrl: ' + webIdeUrl)
                        createIframe(webIdeUrl)
                    } else {
                        toastr.error(response.message);
                    }
                }, function(error) {
                    toastr.error("创建云件失败，请重试！");
                });
            }

            // Experiment
            if ($stateParams.type === '1') {
                var param = {
                    "experimentId": $stateParams.experimentId,
                    "studentId": $stateParams.studentId
                }
                stuCourseSrv.createExCloudware().save(param).$promise.then(function(response) {
                    if (response.errorCode == 0) {
                        $scope.loaded = true;
                        $scope.webideServiceUrl = response.data.webSocket;
                        $scope.webideServiceId = response.data.serviceId;
                        $scope.webideServiceName = response.data.serviceName;
                        var webIdeUrl = $scope.webideBaseUrl + $stateParams.studentId+ '_' + $stateParams.experimentId + "/?wsUrl=" +  $scope.webideServiceUrl
                        console.log('webIdeUrl: ' + webIdeUrl)
                        createIframe(webIdeUrl)
                    } else {
                        toastr.error(response.message);
                    }
                }, function(error) {
                    toastr.error("创建云件失败，请重试！");
                });
            }
        }

        var deleteWebide= function () {
            stuCourseSrv.deleteExCloudware().save({
                studentId: $stateParams.studentId,
                experimentId: $stateParams.experimentId
            })
        }


        var createIframe = function (url) {
            var oldIframe = document.getElementById("iframe");
            if (oldIframe != null) {
                document.getElementById("design").removeChild(oldIframe);
            }

            var iframe = document.createElement('iframe');
            iframe.id = "iframe";
            iframe.src = url;
            iframe.style = "height:100%;width:100%";
            document.getElementById("design").appendChild(iframe);
        }


        var init = function () {
            // 创建loading iframe
            var loadingUrl = "http://www.x-lab.ac:13004/";
            createIframe(loadingUrl);

            // 请求webide容器
            if (!$scope.loaded) {
                console.log('Requesting...')
                initRequest()
            }
        }
        init()

        $scope.isLogin = localStorage["logined"] === 'true';

        var startMoving;

        $scope.resizeMouseDown = function ($event) {
            startMoving = true;
        }

        $(document).mouseup(function () {
            startMoving = false;
        })
        $(document).mousemove(function (e) {
            if(startMoving) {
                var originalLeftWidth = $($('#leftNav')[0]).width();
                var currentDesignWidth = $($('#design')[0]).width();
                $($('#leftNav')[0]).width(e.clientX);
                $($('#design')[0]).width(currentDesignWidth - ($($('#leftNav')[0]).width() - originalLeftWidth));
                e.preventDefault();
            }
        })


        if(!$scope.isLogin){
            $scope.loading = false;
            return;
        } else {
            if($stateParams.studentId === 0){
                $state.go('index.main');
            }
        }


        $scope.notFirst = false;
        $scope.getCloudwareInfo = function() {
            switch ($stateParams.type) {
                case "0":
                    stuCourseSrv.getHwCloudwareInfo().get({
                        homeworkId: $stateParams.homeworkId,
                        studentId: $stateParams.studentId
                    }).$promise.then(
                        function(response) {
                            if (response.errorCode == 0) {
                                $scope.notFirst = true;
                                $scope.loading = false
                                $scope.cloudwareInfo = response.data;
                            } else if (response.errorCode == 7 || response.errorCode == 47) {

                                $scope.loading = false
                            } else {
                                toastr.error("获取作业云件信息失败，请重试")
                            }
                        },
                        function(error) {
                            toastr.error("获取作业云件信息失败，请重试")
                        })
                    break;
                case "1":
                    stuCourseSrv.getExCloudwareInfo().get({
                        experimentId: $stateParams.experimentId,
                        studentId: $stateParams.studentId
                    }).$promise.then(
                        function(response) {

                            if (response.errorCode == 0) {

                                $scope.notFirst = true;
                                $scope.loading = false
                                $scope.cloudwareInfo = response.data;
                            } else if (response.errorCode == 35 || response.errorCode == 47) {

                                $scope.loading = false
                            } else {
                                toastr.error("获取实验云件信息失败，请重试")
                            }
                        },
                        function(error) {
                            toastr.error("获取实验云件信息失败，请重试")
                        })
                    break;
                default:
                    break;
            }
        }

        $scope.fullScreen = function() {
            // $('#design').css({ 'position': 'absolute', 'top': '0' });
            // $('body').height($(window).height());
            // $('body').width($(window).width());
            // $('#design').height($(window).height());
            // $('#design').width($(window).width());
            var canvas = document.getElementById('theCanvas')
            var docElm = document.documentElement;
            if (docElm.requestFullscreen) {
                canvas.requestFullscreen();
            } else if (docElm.webkitRequestFullScreen) {
                canvas.webkitRequestFullScreen();
            }

        };


        $scope.getCloudwareInfo();
        $scope.loading = true;


        $scope.fullScreen = function() {
            var canvas = document.getElementById('theCanvas')
            var docElm = document.documentElement;
            if (docElm.requestFullscreen) {
                canvas.requestFullscreen();
            } else if (docElm.webkitRequestFullScreen) {
                canvas.webkitRequestFullScreen();
            }

        };

        $(window).bind('resize', function() {

            $timeout(function() {
                $('#leftNav').height($('#test').height());

            }, 500)
        });

        // 退出时删除容器
        $scope.$on('$destroy', function(){
            deleteWebide();
        })
        $window.onbeforeunload =  deleteWebide;
    }



})();

(function() {
    'use strict';

    angular
        .module('phoenix')
        .controller('pictureLibCtrl', pictureLibCtrl);

    pictureLibCtrl.$inject = ['$scope', '$timeout', '$uibModalInstance', 'photoswipeSrv', 'courseManagementSrv', '$stateParams', 'reqUrl', '$state'];

    function pictureLibCtrl($scope, $timeout, $uibModalInstance, photoswipeSrv, courseManagementSrv, $stateParams, reqUrl, $state) {
        $timeout(function() {
            photoswipeSrv.initPhotoSwipeFromDOM('.my-gallery');

        })

        courseManagementSrv.getModuleLib().get({ moduleId: $stateParams.moduleId })
            .$promise.then(
                function(response) {
                    console.log(response);
                    if (response.errorCode == 0) {
                        $scope.pics = response.data.imageList;
                    }
                },
                function(error) {
                    console.log(error)
                }
            )
        $scope.getObjectURL = function(file) {
            var url = null;
            if (window.createObjectURL != undefined) { // basic
                url = window.createObjectURL(file);
            } else if (window.URL != undefined) { // mozilla(firefox)
                url = window.URL.createObjectURL(file);
            } else if (window.webkitURL != undefined) { // webkit or chrome
                url = window.webkitURL.createObjectURL(file);
            }
            return url;
        }

        $scope.getFile = function(file) {
            // var url = $scope.getObjectURL(file);
            var formdata = new FormData();
            formdata.append('file', file);
            $.ajax({
                type: 'POST',
                url: reqUrl + '/admin/course/experiment/piclib',
                dataType: 'json',
                beforeSend: function(xhr) {
                    // xhr.setRequestHeader('access_token', '1504751421487');
                    xhr.setRequestHeader('Authorization', 'Bearer ' + (localStorage['token'] ? localStorage['token'] : ''));
                },
                processData: false, // Dont process the files
                contentType: false,
                data: formdata,
                success: function(res) {
                    console.log(res)
                    if (res.errorCode == 0) {
                        // toastr.success('上传成功');
                        $scope.picInfo = res.data;
                        console.log($scope.picInfo);
                        courseManagementSrv.addModuleLib().save({
                            height: $scope.picInfo.height,
                            imageUrl: $scope.picInfo.url,
                            moduleId: $stateParams.moduleId,
                            name: $scope.picInfo.name,
                            width: $scope.picInfo.width
                        }).$promise.then(
                            function(response) {
                                if (response.errorCode == 0) {
                                    toastr.success("上传成功")
                                    $scope.pics.push({
                                        height: $scope.picInfo.height,
                                        imageUrl: $scope.picInfo.url,
                                        name: $scope.picInfo.name,
                                        width: $scope.picInfo.width,
                                        resourceId: response.data
                                    });

                                } else {
                                    toastr.error(response.message)
                                }
                            },
                            function(error) {
                                toastr.error("上传失败，请稍后再试")
                            })
                        $scope.$apply();
                    } else if (res.errorCode == 45) {
                        toastr.warning("登录超时！");
                        localStorage['requireLogin'] = true
                        $state.go("index.main", null, { reload: true })
                    } else if (res.errorCode == 46) {
                        toastr.warning("请重新登录！");
                        localStorage['requireLogin'] = true
                        $state.go("index.main", null, { reload: true })
                    } else {
                        toastr.error(res.message);
                    }
                }
            });
            // $scope.pics.push({ des: "new" + new Date().getTime(), url: url });
            $scope.$apply();
        };
        $scope.choosePic = function() {
            $('#picFile').trigger('click');
        }
        $scope.deletePic = function(index, item) {
            courseManagementSrv.deleteLibPic().save({
                "moduleId": $stateParams.moduleId,
                "resourceId": item.resourceId
            }).$promise.then(function(response) {
                console.log(response);
                if (response.errorCode == 0) {
                    toastr.success("删除成功");
                    $scope.pics.splice(index, 1);

                } else {
                    toastr.error(response.message)
                }
            }, function(error) {
                console.log(error);
            })
        }
        $scope.pics = [];
        $scope.supported = false;

        // $scope.textToCopy = 'I can copy by clicking!';

        $scope.success = function(item) {
            toastr.success('Copied!');
            console.log(item);
        };

        $scope.fail = function(err) {
            toastr.error('Error!', err);
        };
        $scope.cancel = function() {
            $uibModalInstance.dismiss('cancel');
        }
        $scope.ok = function() {

            $uibModalInstance.close();
        }
    }
})();
