        function start(wsaddr, el, retryTime) {
            var retryTime = retryTime || 0;
            ws = new WebSocket(wsaddr);

            var instance = {
                canvas: null,
                isFullscreen: false,
                setFullscreen: function() {
                    var docElm = document.documentElement;
                    if (docElm.requestFullscreen) {
                        canvas.requestFullscreen();
                    } else if (docElm.webkitRequestFullScreen) {
                        canvas.webkitRequestFullScreen();
                    }
                    this.isFullscreen = true;
                },
                ws: ws,
                fsapi: ''
            };
            ws.onerror = function(error) {
                console.log("error occurred")
            }
            ws.onclose = function () {
                console.log("close and restart")
                if(!$scope.leavePage) {
                    setTimeout(function() {
                        if (retryTime > 25) {
                            toastr.error("启动云件失败，请重试")
                            $scope.hasStarted = false
                            usSpinnerService.stop('ex-spinner');
                            if ($stateParams.type == 1) { //如果是实验，删除改实验云件
                                $scope.notFirst = false
                                stuCourseSrv.deleteExCloudware().save({
                                    studentId: $stateParams.studentId,
                                    experimentId: $stateParams.experimentId
                                })
                            }
                            $scope.$apply()
                        } else {
                            retryTime++
                            start(wsaddr, el, retryTime)
                        }
                    }, 1000)
                }
            }
            ws.onopen = function() {
                var canvas = document.getElementById("theCanvas")
                if(!canvas) {
                    canvas = document.createElement('canvas')
                    el.appendChild(canvas);
                    canvas.width = 1440;
                    canvas.height = 900;
                    canvas.style.width = '100%';
                    canvas.style.height = '100%';
                    canvas.id = "theCanvas";
                    canvas.tabIndex = 0
                }

                canvas.oncontextmenu = function(e) {
                    return false;
                }
                $('#design').css('height', 'initial');

                $('#leftNav').height($('#design').height());
                usSpinnerService.stop('ex-spinner');
                instance.canvas = canvas;

                canvas.focus();
                canvas.onmousemove = function(e) {
                    e.preventDefault();
                    var dom_left = canvas.offsetLeft + canvas.offsetParent.offsetLeft;
                    var dom_top = canvas.offsetTop + canvas.offsetParent.offsetTop;
                    var scroll_top = el.scrollTop;
                    if (instance.isFullscreen) {
                        scroll_top = 0;
                    }
                    var xbei = canvas.offsetWidth / 1440;
                    var ybei = canvas.offsetHeight / 900;
                    var x = Math.floor((e.pageX - dom_left) / xbei);
                    var y = Math.floor((e.pageY - dom_top + scroll_top) / ybei);
                    var buf = new ArrayBuffer(5);
                    var dv = new DataView(buf);
                    dv.setUint8(0, 0);
                    dv.setUint16(1, x, true);
                    dv.setUint16(3, y, true);
                    if (ws.readyState == 1)
                        ws.send(buf);
                };
                canvas.onmousedown = function(e) {
                    e.preventDefault();
                    canvas.focus();
                    var buf = new ArrayBuffer(5);
                    var dv = new DataView(buf);
                    dv.setUint8(0, 1);
                    dv.setUint32(1, e.which, true);
                    if (ws.readyState == 1)
                        ws.send(buf);
                };
                canvas.onmouseup = function(e) {
                    e.preventDefault();
                    var buf = new ArrayBuffer(5);
                    var dv = new DataView(buf);
                    dv.setUint8(0, 2);
                    dv.setUint32(1, e.which, true);
                    if (ws.readyState == 1)
                        ws.send(buf);
                };
                canvas.onkeydown = function(e) {
                    e.preventDefault();
                    console.log("key:" + e.key + " which:" + e.which + " mapto: " + (mapKey(e.which)-8));
                    if(e.which === 144) // do not send num lock
                        return;
                    var buf = new ArrayBuffer(5);
                    var dv = new DataView(buf);
                    dv.setUint8(0, 3);
                    dv.setUint32(1, mapKey(e.which), true);
                    if (ws.readyState == 1)
                        ws.send(buf);
                };
                canvas.onkeyup = function(e) {
                    e.preventDefault();
                    if(e.which === 144) // do not send num lock
                        return;
                    var buf = new ArrayBuffer(5);
                    var dv = new DataView(buf);
                    dv.setUint8(0, 4);
                    dv.setUint32(1, mapKey(e.which), true);
                    if (ws.readyState == 1)
                        ws.send(buf);
                };
            };
            ws.binaryType = "arraybuffer";
            ws.onmessage = function(msg) {
                var data = msg.data;
                if (!(data instanceof ArrayBuffer)) {
                    return
                }
                var dv = new DataView(data);

                var x = dv.getInt32(0, true);
                var y = dv.getInt32(4, true);
                var d = data.slice(8);
                var blob = new Blob([d], { type: 'image/webp' });
                var url = URL.createObjectURL(blob);
                var img = new Image;
                img.onload = function() {
                    var ctx = instance.canvas.getContext('2d');
                    ctx.drawImage(img, x, y);
                    URL.revokeObjectURL(url);
                };
                img.src = url;
            };
        }

        $scope.$on('$destroy', function() {
            if(ws){
                ws.close()
            }
            $scope.leavePage = true
        });
        $scope.onExit = function() {
            if(ws){
                ws.close()
            }
            $scope.leavePage = true
        };

        $window.onbeforeunload =  $scope.onExit;

        var startService = function(studentId, type) {
            if(type == "1") {
                stuCourseSrv.getLastExperiment().get({
                    studentId: localStorage['userId']
                }, function (response) {
                    if (response.errorCode == 0) {
                        //如果上次实验存在且与本次实验id不一致，提醒用户删除上一次实验容器
                        if (response.data.lastExperimentId != 0 &&
                            response.data.lastExperimentId != $stateParams.experimentId) {
                            swal({
                                    title: "注意",
                                    text: "上一次实验【" + response.data.lastModuleName + "/" + response.data.lastExperimentName + "】还未关闭。\n目前一个用户仅能同时打开1个实验容器\n是否关闭上一个实验容器？",
                                    type: "warning",
                                    showCancelButton: true,
                                    confirmButtonColor: "#DD6B55",
                                    confirmButtonText: "确定",
                                    closeOnConfirm: true,
                                    cancelButtonText: "取消",
                                    closeOnCancel: true
                                },
                                function (isConfirm) {
                                    if (isConfirm) {
                                        stuCourseSrv.deleteExCloudware().save({
                                            studentId: localStorage['userId'],
                                            experimentId: response.data.lastExperimentId
                                        }, function (response) {
                                            if (response.errorCode == 0) {
                                                toastr.success("删除上次实验成功")
                                                createCloudware(studentId, type)
                                            } else {
                                                toastr.error("删除上次实验失败，请重试")
                                            }
                                        })
                                    } else {
                                        history.go(-1)
                                    }
                                });
                        } else {
                            //如果上次实验存在且与本次实验id一致或没做过实验，直接开启
                            createCloudware(studentId, type)
                        }
                    }
                })
            } else {
                createCloudware(studentId, type)
            }
        }

        function createCloudware(studentId, type) {
            if ($('[data-cloudware-env]').length > 0) {
                $('[data-cloudware-env]').each(function(index, el) {

                    switch (type) {
                        case "0":
                            stuCourseSrv.createHwCloudware().save({
                                "homeworkId": $stateParams.homeworkId,
                                "studentId": studentId,
                            }).$promise.then(function(response) {
                                if (response.errorCode == 0) {
                                    start(response.data.webSocket, el)
                                    $scope.notFirst = true
                                    $scope.cloudwareInfo = $scope.cloudwareInfo || {}
                                    $scope.cloudwareInfo.webSocket = response.data.webSocket
                                } else {
                                    toastr.error("创建云件失败，请重试！");
                                }
                            }, function(error) {
                                toastr.error("创建云件失败，请重试！");
                            });
                            break;
                        case "1":
                            stuCourseSrv.createExCloudware().save({
                                "experimentId": $stateParams.experimentId,
                                "studentId": studentId,
                            }).$promise.then(function(response) {
                                if (response.errorCode == 0) {
                                    start(response.data.webSocket, el)
                                    $scope.notFirst = true
                                    $scope.cloudwareInfo = $scope.cloudwareInfo || {}
                                    $scope.cloudwareInfo.webSocket = response.data.webSocket
                                } else {
                                    toastr.error("创建云件失败，请重试！");
                                }
                            }, function(error) {
                                toastr.error("创建云件失败，请重试！");
                            });
                            break;

                        default:
                            break;
                    }
                });

            } else {
                startService();
            }
        }
